import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Conduct from '@/components/Conduct'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/rules',
      name: 'Conduct',
      component: Conduct
    }
  ]
})



